                    <span class="hljs-tag">&lt;<span class="hljs-name">MenuItem</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"1-3"</span>&gt;</span>
                        <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"settings"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Icon</span>&gt;</span>
                        <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>Option 3<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
                    <span class="hljs-tag">&lt;/<span class="hljs-name">MenuItem</span>&gt;</span>
                <span class="hljs-tag">&lt;/<span class="hljs-name">Menu</span>&gt;</span>
            <span class="hljs-tag">&lt;/<span class="hljs-name">Sider</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">Layout</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">Header</span> <span class="hljs-attr">:style</span>=<span class="hljs-string">"{background: '#fff', boxShadow: '0 2px 3px 2px rgba(0,0,0,.1)'}"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Header</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">Content</span> <span class="hljs-attr">:style</span>=<span class="hljs-string">"{padding: '0 16px 16px'}"</span>&gt;</span>
                    <span class="hljs-tag">&lt;<span class="hljs-name">Breadcrumb</span> <span class="hljs-attr">:style</span>=<span class="hljs-string">"{margin: '16px 0'}"</span>&gt;</span>
                        <span class="hljs-tag">&lt;<span class="hljs-name">BreadcrumbItem</span>&gt;</span>Home<span class="hljs-tag">&lt;/<span class="hljs-name">BreadcrumbItem</span>&gt;</span>
                        <span class="hljs-tag">&lt;<span class="hljs-name">BreadcrumbItem</span>&gt;</span>Components<span class="hljs-tag">&lt;/<span class="hljs-name">BreadcrumbItem</span>&gt;</span>
                        <span class="hljs-tag">&lt;<span class="hljs-name">BreadcrumbItem</span>&gt;</span>Layout<span class="hljs-tag">&lt;/<span class="hljs-name">BreadcrumbItem</span>&gt;</span>
                    <span class="hljs-tag">&lt;/<span class="hljs-name">Breadcrumb</span>&gt;</span>
                    <span class="hljs-tag">&lt;<span class="hljs-name">Card</span>&gt;</span>
                        <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"height: 600px"</span>&gt;</span>Content<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
                    <span class="hljs-tag">&lt;/<span class="hljs-name">Card</span>&gt;</span>
                <span class="hljs-tag">&lt;/<span class="hljs-name">Content</span>&gt;</span>
            <span class="hljs-tag">&lt;/<span class="hljs-name">Layout</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">Layout</span>&gt;</span>
