<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基本结构1">基本结构</a></li><li><a href="#vue-doc-上中下布局1">上中下布局</a></li><li><a href="#vue-doc-顶部侧边布局通栏1">顶部-侧边布局-通栏</a></li><li><a href="#vue-doc-顶部侧边布局1">顶部-侧边布局</a></li><li><a href="#vue-doc-侧边布局1">侧边布局</a></li><li><a href="#vue-doc-自定义触发器1">自定义触发器</a></li><li><a href="#vue-doc-响应式布局1">响应式布局</a></li><li><a href="#vue-doc-固定头部1">固定头部</a></li><li><a href="#vue-doc-固定侧边栏1">固定侧边栏</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
