        <div class="vue-doc-content">
          <h2 id="vue-doc-设计规则1" class="vue-doc-element">设计规则</h2>
<h3 id="vue-doc-尺寸1_1" class="vue-doc-element">尺寸</h3>
<p class="vue-doc-element">一级导航项偏左靠近 <code class="vue-doc-element">logo</code> 放置，辅助菜单偏右放置。</p>
<ul class="vue-doc-element">
<li>顶部导航（大部分系统）：一级导航高度 64px，二级导航 48px。</li>
<li>顶部导航（展示类页面）：一级导航高度 80px，二级导航 56px。</li>
<li>顶部导航高度的范围计算公式为：48+8n。</li>
<li>侧边导航宽度的范围计算公式：200+8n。<h3 id="vue-doc-交互1_2" class="vue-doc-element">交互</h3>
</li>
<li>一级导航和末级的导航需要在可视化的层面被强调出来；</li>
<li>当前项应该在呈现上优先级最高；</li>
<li>当导航收起的时候，当前项的样式自动赋予给它的上一个层级；</li>
<li>左侧导航栏的收放交互同时支持手风琴和全展开的样式，根据业务的要求进行适当的选择。<h3 id="vue-doc-视觉1_3" class="vue-doc-element">视觉</h3>
导航样式上需要根据信息层级合理的选择样式：</li>
</ul>
<h4 id="vue-doc-大色块强调" class="vue-doc-element">大色块强调</h4>
<p class="vue-doc-element">建议用于底色为深色系时，当前页面父级的导航项。</p>
<h4 id="vue-doc-高亮火柴棍" class="vue-doc-element">高亮火柴棍</h4>
<p class="vue-doc-element">当导航栏底色为浅色系时使用，可用于当前页面对应导航项，建议尽量在导航路径的最终项使用。</p>
<h4 id="vue-doc-字体高亮变色" class="vue-doc-element">字体高亮变色</h4>
<p class="vue-doc-element">从可视化层面，字体高亮的视觉强化力度低于大色块，通常在当前项的上一级使用。</p>
<h4 id="vue-doc-字体放大" class="vue-doc-element">字体放大</h4>
<p class="vue-doc-element">12px、14px 是导航的标准字号，14 号字体用在一、二级导航中。字号可以考虑导航项的等级做相应选择。</p>
<h3 id="vue-doc-组件概述1_4" class="vue-doc-element">组件概述</h3>
<ul class="vue-doc-element">
<li>Layout：布局容器，其下可嵌套 HeaderSiderContentFooter或 Layout 本身，可以放在任何父容器中。</li>
<li>Header：顶部布局，自带默认样式，其下可嵌套任何元素，只能放在 Layout 中。</li>
<li>Sider：侧边栏，自带默认样式及基本功能，其下可嵌套任何元素，只能放在 Layout 中。</li>
<li>Content：内容部分，自带默认样式，其下可嵌套任何元素，只能放在 Layout 中。</li>
<li>Footer：底部布局，自带默认样式，其下可嵌套任何元素，只能放在 Layout 中。</li>
</ul>
<h2 id="vue-doc-api2" class="vue-doc-element">API</h2>
<p class="vue-doc-element">Sider props</p>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>breakpoint</td>
<td>触发响应式布局的断点，可选值为xs,sm,md,lg,xl或xxl，若不设此属性则不会触发响应式布局。</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>value</td>
<td>侧边栏是否收起，可使用 v-model 双向绑定数据。</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>width</td>
<td>宽度</td>
<td>Number</td>
<td>200</td>
</tr>
<tr>
<td>collapsible</td>
<td>是否可收起，设为false后，默认触发器会隐藏，且响应式布局不会触发</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>collapsed-width</td>
<td>收缩宽度，设置为 0 会出现特殊 trigger</td>
<td>Number</td>
<td>64</td>
</tr>
<tr>
<td>hide-trigger</td>
<td>隐藏默认触发器</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>default-collapsed</td>
<td>是否默认收起，设置了collapsible后设置此属性侧边栏仍会收起。</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>reverse-arrow</td>
<td>改变侧边栏触发器箭头方向，和改变侧边栏收起方向，当Sider在右边时可以使用。</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<p class="vue-doc-element">Sider events</p>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-collapse</td>
<td>展开-收起时的回调</td>
<td>true / false</td>
</tr>
</tbody>
</table>
<p class="vue-doc-element">Sider slot</p>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>trigger</td>
<td>自定义触发器</td>
</tr>
</tbody>
</table>
<p class="vue-doc-element">Sider methods</p>
<table class="vue-doc-element">
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>toggleCollapse</td>
<td>改变Sider展开-收起状态。</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demolayout1 from './demo/layout1.md';
import demolayout2 from './demo/layout2.md';
import demolayout3 from './demo/layout3.md';
import demolayout4 from './demo/layout4.md';
import demolayout5 from './demo/layout5.md';
import demolayout6 from './demo/layout6.md';
import demolayout7 from './demo/layout7.md';
import demolayout8 from './demo/layout8.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demolayout1,demolayout2,demolayout3,demolayout4,demolayout5,demolayout6,demolayout7,demolayout8
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


