        <span class="hljs-attribute">height</span>: <span class="hljs-number">30px</span>;
        <span class="hljs-attribute">background</span>: <span class="hljs-number">#5b6270</span>;
        <span class="hljs-attribute">border-radius</span>: <span class="hljs-number">3px</span>;
        <span class="hljs-attribute">float</span>: left;
        <span class="hljs-attribute">position</span>: relative;
        <span class="hljs-attribute">top</span>: <span class="hljs-number">15px</span>;
        <span class="hljs-attribute">left</span>: <span class="hljs-number">20px</span>;
    }
    <span class="hljs-selector-class">.layout-nav</span>{
        <span class="hljs-attribute">width</span>: <span class="hljs-number">420px</span>;
        <span class="hljs-attribute">margin</span>: <span class="hljs-number">0</span> auto;
        <span class="hljs-attribute">margin-right</span>: <span class="hljs-number">20px</span>;
    }
    <span class="hljs-selector-class">.layout-footer-center</span>{
        <span class="hljs-attribute">text-align</span>: center;
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"layout"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Layout</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">Header</span> <span class="hljs-attr">:style</span>=<span class="hljs-string">"{position: 'fixed', width: '100%'}"</span>&gt;</span>
                <span class="hljs-tag">&lt;<span class="hljs-name">Menu</span> <span class="hljs-attr">mode</span>=<span class="hljs-string">"horizontal"</span> <span class="hljs-attr">theme</span>=<span class="hljs-string">"dark"</span> <span class="hljs-attr">active-name</span>=<span class="hljs-string">"1"</span>&gt;</span>
                    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"layout-logo"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
                    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"layout-nav"</span>&gt;</span>
                        <span class="hljs-tag">&lt;<span class="hljs-name">MenuItem</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"1"</span>&gt;</span>
                            <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"ios-navigate"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Icon</span>&gt;</span>
                            Item 1
                        <span class="hljs-tag">&lt;/<span class="hljs-name">MenuItem</span>&gt;</span>
                        <span class="hljs-tag">&lt;<span class="hljs-name">MenuItem</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"2"</span>&gt;</span>
                            <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"ios-keypad"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Icon</span>&gt;</span>
