            <span class="hljs-tag">&lt;/<span class="hljs-name">Content</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">Footer</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"layout-footer-center"</span>&gt;</span>2011-2016 &amp;copy; TalkingData<span class="hljs-tag">&lt;/<span class="hljs-name">Footer</span>&gt;</span>
